import Navbar from './components/Navbar';
import Slider from './components/Slider';

const App = () => {

const preloader = (
  <div id="preload">
    <div id="loader"><img src={process.env.PUBLIC_URL + "/avatar_head.png"} alt='avatar'/></div>
  </div>
)

  return (
    <>
      {preloader}
      <Navbar />
      <Slider />
    </>
  )
}

export default App
